html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #fff;
}

p {
  font-size: 2rem;
  color: #5A6473;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  padding: 5rem 0;
  background-color: #E6F0FF;
}
.header__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__flex {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
}
.header__logo {
  text-align: center;
  border-right: 6px solid #000006;
  border-bottom: 6px solid #000006;
  color: #000339;
  transition-property: transform box-shadow;
  transition-duration: 0.3s;
}
.header__logo:hover {
  transform: translateX(20px);
  box-shadow: -12px -12px 0px #000006;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
  }
}

.navegacion {
  margin-top: 3rem;
}
.navegacion__enlaces {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .navegacion__enlaces {
    flex-direction: row;
  }
}
@media (min-width: 1200px) {
  .navegacion__enlaces {
    gap: 2rem;
  }
}
.navegacion__opciones {
  margin-top: 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 3rem;
}
.navegacion__opcion {
  font-weight: 600;
  color: #000339;
}
.navegacion__opcion--boton {
  background-color: #000339;
  font-size: 1.6rem;
  padding: 1.4rem;
  border-radius: 1rem;
  font-weight: 600;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
  border: none;
}
.navegacion__opcion--boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .navegacion__opcion--boton {
    width: auto;
  }
}
.navegacion__enlace {
  color: #000339;
}

.portada {
  padding: 5rem 0;
}
.portada__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .portada__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 6rem;
  }
}
.portada__imagen {
  width: 70%;
}
@media (min-width: 768px) {
  .portada__imagen {
    width: 100%;
  }
}
.portada__heading {
  font-size: 5rem;
  color: #000339;
}
.portada__info {
  font-size: 1.8rem;
  line-height: 1.4;
}
.portada__more {
  background-color: #000339;
  font-size: 1.6rem;
  padding: 1.4rem;
  border-radius: 1rem;
  font-weight: 600;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
  border: none;
  font-weight: 300;
  margin-top: 2rem;
}
.portada__more:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .portada__more {
    width: auto;
  }
}
@media (min-width: 768px) {
  .portada__more {
    width: 40%;
  }
}

.principal {
  padding: 5rem 0;
}
.principal__flex {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .principal__flex {
    flex-direction: row;
    gap: 0;
    justify-content: space-around;
    align-items: flex-end;
    gap: 40rem;
    text-align: left;
  }
}
.principal__logo, .principal__description, .principal__rating {
  margin: 1rem 0 0 0;
}

.sponsors {
  padding: 5rem 0;
}
.sponsors__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .sponsors__grid {
    flex-direction: row;
    gap: 10rem;
    align-items: center;
  }
}

.sponsor {
  width: 20%;
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .sponsor {
    width: 90%;
  }
}
.sponsor__image {
  filter: grayscale(100%);
  transition-property: filter transform;
  transition-duration: 0.3s;
}
.sponsor__image:hover {
  transform: scale(3);
  filter: grayscale(0%);
}
@media (min-width: 1200px) {
  .sponsor__image:hover {
    transform: scale(1.2);
  }
}

.how {
  padding: 5rem;
}
.how__linea {
  width: 10rem;
  background-color: #000339;
  height: 0.5rem;
  border-radius: 1rem;
  margin: 0 auto;
}
.how__heading {
  text-align: center;
  color: #000339;
}
.how__info {
  text-align: center;
  line-height: 2;
}
@media (min-width: 768px) {
  .how__info--space {
    display: block;
  }
}
.how__grid {
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8rem;
}
@media (min-width: 768px) {
  .how__grid {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.how__boton {
  margin-top: 8rem;
  text-align: center;
  background-color: #000339;
  font-size: 1.6rem;
  padding: 1.4rem;
  border-radius: 1rem;
  font-weight: 600;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
  border: none;
}
.how__boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .how__boton {
    width: auto;
  }
}
@media (min-width: 768px) {
  .how__boton {
    width: 30%;
    margin: 8rem auto 0 auto;
  }
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.item__imagen {
  margin: 0 auto;
}
.item__heading {
  color: #5A6473;
  font-weight: 400;
  font-size: 2rem;
  margin: 2rem 0 0 0;
  text-align: center;
}
@media (min-width: 1200px) {
  .item__heading {
    font-size: 2.6rem;
  }
}

.htb {
  display: flex;
  gap: 1.3rem;
  align-items: center;
  justify-content: center;
}
.htb__info {
  font-size: 1.7rem;
  color: #0689FF;
  margin: 1.5rem 0 0 0;
}
.htb__image {
  margin-top: 1.5rem;
}
.htb:hover {
  cursor: pointer;
}

.featured {
  background-color: #E6F0FF;
  padding: 5rem 0;
}
.featured__heading {
  color: #000339;
  text-align: center;
}
@media (min-width: 768px) {
  .featured__heading {
    text-align: left;
  }
}
.featured__info {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .featured__info {
    flex-direction: row;
    justify-content: space-between;
  }
}
.featured__text {
  text-align: center;
}
@media (min-width: 768px) {
  .featured__text {
    text-align: left;
  }
}
.featured__grid {
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .featured__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.wap {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}
.wap__info {
  color: #0689FF;
  font-weight: 700;
}

.property {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
  transition-property: scale rotate;
  transition-duration: 0.3s;
}
@media (min-width: 1200px) {
  .property {
    width: 90%;
  }
}
.property:hover {
  transform: scale(1.1);
}
.property__info {
  padding: 2rem;
}
.property__price {
  color: #000339;
  font-size: 2rem;
}
.property__address {
  font-size: 1.8rem;
  color: #A3A6AB;
}

.espacio__flex {
  display: flex;
  gap: 3rem;
  align-items: center;
}
.espacio__habitaciones, .espacio__both, .espacio__superficie {
  display: flex;
  gap: 0.5rem;
}
.espacio__info {
  font-size: 1.4rem;
  margin: 0;
}

.porcentajes {
  padding: 5rem 0 0 0;
  line-height: 1.5;
}
.porcentajes .porcentajes__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .porcentajes .porcentajes__grid {
    display: grid;
    grid-template-columns: 4fr 2fr;
  }
}
.porcentajes__raya {
  width: 5rem;
  height: 0.5rem;
  background-color: #000339;
  border-radius: 3rem;
  margin: 0 auto 0 0;
}
@media (min-width: 768px) {
  .porcentajes__info {
    margin-bottom: 12rem;
  }
}
.porcentajes__heading {
  color: #000339;
}

.numeros__grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .numeros__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .numeros__grid {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.numero__cantidad {
  font-size: 10rem;
  font-weight: 400;
  margin: 0;
}
.numero__info {
  margin: 2rem 0 0 0;
}
.numero__cantidad, .numero__info {
  text-align: center;
}
.numero:nth-child(3) {
  grid-column: 1/3;
  text-align: center;
}

.start {
  margin-top: 15rem;
  padding: 5rem;
  background-color: #063183;
  border-radius: 2rem;
  position: relative;
  height: 40rem;
}
@media (min-width: 768px) {
  .start {
    height: 35rem;
  }
}
.start__image {
  display: none;
}
@media (min-width: 1200px) {
  .start__image {
    display: unset;
    position: absolute;
    right: 0;
    top: -5rem;
  }
}
.start__heading {
  color: #fff;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  text-align: center;
}
@media (min-width: 1200px) {
  .start__heading {
    text-align: left;
  }
}
.start__text {
  color: #fff;
  font-weight: 300;
  text-align: center;
}
@media (min-width: 1200px) {
  .start__text {
    text-align: left;
  }
}
.start__boton {
  background-color: #fff;
  font-size: 1.6rem;
  padding: 1.4rem;
  border-radius: 1rem;
  font-weight: 600;
  display: block;
  width: 100%;
  text-align: center;
  color: #063183;
  text-decoration: none;
  transition: color 0.3s ease;
  border: none;
}
.start__boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .start__boton {
    width: auto;
  }
}
@media (min-width: 768px) {
  .start__boton {
    width: 100%;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .start__boton {
    width: 20%;
    margin: 0 auto 0 0;
  }
}

.footer {
  padding: 10rem 0;
}
.footer__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8rem;
}
@media (min-width: 768px) {
  .footer__grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: unset;
  }
}

.option__heading {
  font-weight: 400;
  font-size: 2.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .option__heading {
    text-align: left;
  }
}
.option__items {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .option__items {
    align-items: unset;
  }
}
.option__item {
  color: #5A6473;
}

.iconos {
  display: flex;
  gap: 4rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .iconos {
    justify-content: unset;
  }
}

.copy {
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .copy {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
  }
}
.copy__logo {
  color: #000339;
  margin: 0;
}
.copy__logo:hover {
  cursor: pointer;
}
.copy__text {
  color: #5A6473;
  margin: 0;
}