// Rating

.principal
{
    padding: $separacion 0;

    &__flex
    {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        text-align: center;

        @include tablet 
        {
            flex-direction: row;
            gap: 0;
            justify-content: space-around;
            align-items: flex-end;
            gap: 40rem;
            text-align: left;
        }
    }

    &__logo,
    &__description,
    &__rating
    {
        margin: 1rem 0 0 0;
    }

}

// Sponsors

.sponsors
{
    padding: $separacion 0;
    

    &__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        @include tablet 
        {
            flex-direction: row;
            gap: 10rem;
            align-items: center;
        }
        
    }
}

.sponsor
{
    width: 20%;
    margin: 0 auto;

    @include desktop 
    {
        width: 90%;
    }

    &__image
    {
        filter: grayscale(100%);
        transition-property: filter transform;
        transition-duration: .3s;

        &:hover
        {
            transform: scale(3);
            filter: grayscale(0%);
            @include desktop 
            {
                transform: scale(1.2);
            }
            
        }
    }
}

// How it works
.how
{
    padding: $separacion;

    &__linea
    {
        width: 10rem;
        background-color: $primario;
        height: .5rem;
        border-radius: 1rem;
        margin: 0 auto;
    }

    &__heading
    {
        text-align: center;
        color: $primario;
    }

    &__info
    {
        text-align: center;
        line-height: 2;

        &--space
        {

            @include tablet 
            {
                display: block;
            }
        }
    }

    &__grid
    {
        margin-top: $separacion * 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 8rem;

        @include tablet 
        {
            @include grid(3, 10rem);
        }
    }

    &__boton
    {
        margin-top: 8rem;
        text-align: center;

        @include boton($primario, $blanco);
        
        @include tablet 
        {
            width: 30%;
            margin: 8rem auto 0 auto;
        }
    }
}

.item
{
    display: flex;
    flex-direction: column;
    align-items: center;

    &__imagen
    {
        margin: 0 auto;
    }

    &__heading
    {
        color: $parrafos;
        font-weight: $regular;
        font-size: 2rem;
        margin: 2rem 0 0 0;
        text-align: center;

        @include desktop 
        {
            font-size: 2.6rem;
        }
    }
}

.htb
{
    display: flex;
    gap: 1.3rem;
    align-items: center;
    justify-content: center;

    &__info
    {
        font-size: 1.7rem;
        color: $terciario;
        margin: 1.5rem 0 0 0;
    }

    &__image
    {
        margin-top: 1.5rem;
    }

    &:hover
    {
        cursor: pointer;
    }
}