.porcentajes
{
    padding: $separacion 0 0 0;
    line-height: 1.5;

    .porcentajes__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet 
        {
            display: grid;
            grid-template-columns: 4fr 2fr;
        }
    }

    &__raya
    {
        width: 5rem;
        height: .5rem;
        background-color: $primario;
        border-radius: 3rem;
        margin: 0 auto 0 0;
    }

    &__info
    {

        @include tablet 
        {
            margin-bottom: 12rem;
        }
    }

    &__heading
    {
        color: $primario;
    }
}

.numeros
{
    &__grid
    {
        display: flex;
        flex-direction: column;

        @include tablet 
        {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }


        @include desktop 
        {   
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }
}

.numero
{

    &__cantidad
    {
        font-size: 10rem;
        font-weight: $regular;
        margin: 0;
    }

    &__info
    {
        margin: 2rem 0 0 0;
    }

    &__cantidad,
    &__info
    {
        text-align: center;
    }

    &:nth-child(3)
    {
        grid-column: 1 / 3;
        text-align: center;
    }
}

.start
{
    margin-top: $separacion * 3;
    padding: $separacion;
    background-color: $fondoNews;
    border-radius: 2rem;
    position: relative;
    height: 40rem;

    @include tablet 
    {    
        height: 35rem;
    }

    &__image
    {
        display: none;
        @include desktop 
        {
            display: unset;
            position: absolute;
            right: 0;
            top: -5rem;  
        }
    }

    &__heading
    {
        color: $blanco;
        font-weight: $semiBold;
        font-family: $fuentePrincipal;
        
        text-align: center;

        @include desktop 
        {
            text-align: left;
        }
    }

    &__text
    {
        color: $blanco;
        font-weight: $fina;
        text-align: center;

        @include desktop 
        {
            text-align: left;
        }
    }

    &__boton
    {
        @include boton($blanco, $fondoNews);

        @include tablet 
        {
            width: 100%;
            margin: 0 auto;
        }

        @include desktop 
        {
            width: 20%;
            margin: 0 auto 0 0;
        }
    }
}