.header
{
    padding: $separacion 0;
    background-color: $bg-color;

    &__flex
    {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet 
        {
            flex-direction: row;
            align-items:  flex-start;
            justify-content: space-between;
        }
    }

    &__logo
    {
        text-align: center;
        border-right: 6px solid darken($primario, 10%);
        border-bottom: 6px solid darken($primario, 10%);
        color: $primario;
        transition-property: transform box-shadow;
        transition-duration: .3s;

        &:hover
        {
            transform: translateX(20px);
            box-shadow: -12px -12px 0px darken($primario, 10%);
        }

        @include tablet 
        {
            text-align: left;
        }
    }
}

.navegacion
{
    margin-top: 3rem;

    &__enlaces
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        @include tablet 
        {
            flex-direction: row;
        }

        @include desktop 
        {
            gap: 2rem;
        }
    }

    &__opciones
    {
        margin-top: 3rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        column-gap: 3rem;
    }

    &__opcion
    {
        font-weight: 600;
        color: $primario;

        &--boton
        {
            @include boton($primario, $blanco);
        }
    }

    &__enlace
    {
        color: $primario;
    }

}