.footer
{
    padding: $separacion * 2 0;

    &__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 8rem;

        @include tablet 
        {
            flex-direction: row;
            justify-content: space-between;
            align-items: unset;
        }
    }
}

.option
{

    &__heading
    {
        font-weight: $regular;
        font-size: 2.4rem;
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__items
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        @include tablet 
        {
            align-items: unset;
        }
    }

    &__item
    {
        color: $parrafos;
    }
}

.iconos
{
    display: flex;
    gap: 4rem;
    justify-content: center;

    @include tablet 
    {
        justify-content: unset;
    }
}

.copy
{
    padding-top: 5rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;

    @include tablet 
    {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 0;
    }

    &__logo
    {
        color: $primario;
        margin: 0;

        &:hover
        {
            cursor: pointer;
        }
    }

    &__text
    {
        color: $parrafos;
        margin: 0;
    }
}