.featured
{
    background-color: $bg-color;
    padding: $separacion 0;

    &__heading
    {
        color: $primario;
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__info
    {
        display: flex;
        flex-direction: column;

        @include tablet 
        {
            flex-direction: row;
            justify-content: space-between;   
        }
    }

    &__text
    {
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__grid
    {
        padding-top: $separacion;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include tablet 
        {
            @include grid(3, 4rem)
        }
    }
}

.wap
{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;

    &__info
    {
        color: $terciario;
        font-weight: $bold;
    }
}

.property
{
    background-color: $blanco;
    display: flex;
    flex-direction: column;
    margin-top: 3rem;

    @include desktop 
    {
        width: 90%;
    }

    transition-property: scale rotate;
    transition-duration: .3s;

    &:hover
    {
        transform: scale(1.1);
    }

    &__info
    {
        padding: 2rem;
    }
    &__price
    {
        color: $primario;
        font-size: 2rem;
    }

    &__address
    {
        font-size: 1.8rem;
        color: $ubicacionCasa;
    }
}

.espacio
{

    &__flex
    {
        display: flex;
        gap: 3rem;
        align-items: center;
    }

    &__habitaciones,
    &__both,
    &__superficie
    {
        display: flex;
        gap: .5rem;
    }

    &__info
    {
        font-size: 1.4rem;
        margin: 0;
    }
}