.portada
{
    padding: $separacion 0;

    &__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet 
        {
            @include grid(2, 3rem);
            gap: 6rem;
        }
    }

    &__imagen
    {
        width: 70%;

        @include tablet 
        {
            width: 100%;
        }
    }

    &__heading
    {
        font-size: 5rem;
        color: $primario;
    }

    &__info
    {
        font-size: 1.8rem;
        line-height: 1.4;
    }

    &__more
    {
        @include boton($primario, $blanco);
        font-weight: $fina;
        margin-top: 2rem;

        @include tablet 
        {
            width: 40%;
        }
    }
}